import React, { useState, useEffect, useRef, useCallback } from 'react';
import { delay, propTypeValidation } from './utils';
import './DialogBox.css';

const DialogBox = (props) => {
  const [text, setText] = useState('');
  const [blink, setBlink] = useState(false);
  const [hideCursor, setHideCursor] = useState(true);
  const typeSpeedDelayRef = useRef(null);
  const loopingRef = useRef(false);
  const audioPlayingRef = useRef(false);

  const animateText = useCallback(async () => {
    const runAnimation = async (str) => {
      const textArr = typeof str == 'string' && str.trim().split('');
      if (textArr) {
        setBlink(false);
        let currentText = '';
        const typeSpeedDelay = new delay(props.typeSpeed || 15);
        typeSpeedDelayRef.current = typeSpeedDelay;

        for (let char = 0; char < textArr.length; char++) {
          try {
            await typeSpeedDelay.getPromise();
            if (!loopingRef.current && props.loop !== true) {
              // Animation was cancelled
              return;
            }
            // Safely play audio with promise handling
            const audioElement = document.querySelector('#typingSound');
            if (audioElement && !audioPlayingRef.current) {
              audioPlayingRef.current = true;
              audioElement.play().catch(() => {
                // Ignore play errors (e.g., interrupted by pause)
                audioPlayingRef.current = false;
              });
            }
            currentText += textArr[char];
            setText(currentText);
          } catch (error) {
            // Animation was cancelled
            return;
          }
        }
        // Safely pause audio
        const audioElement = document.querySelector('#typingSound');
        if (audioElement && audioPlayingRef.current) {
          audioElement.pause();
          audioPlayingRef.current = false;
        }
        setBlink(true);
      }
    };

    try {
      const shouldLoop = props.loop || false;
      setHideCursor(false);
      loopingRef.current = true;

      do {
        await runAnimation(props.text);
      } while (shouldLoop && loopingRef.current);

      if (props.hideCursorAfterText) {
        setHideCursor(true);
      }
    } catch (error) {}
  }, [props.text, props.loop, props.hideCursorAfterText, props.typeSpeed]);

  useEffect(() => {
    // Reset state when text changes
    setText('');
    setBlink(false);
    
    // Cancel any ongoing animation
    if (typeSpeedDelayRef.current) {
      typeSpeedDelayRef.current.cancel();
    }
    loopingRef.current = false;
    
    // Stop audio if playing
    const audioElement = document.querySelector('#typingSound');
    if (audioElement && audioPlayingRef.current) {
      audioElement.pause();
      audioPlayingRef.current = false;
    }
    
    // Start new animation
    if (props.text) {
      animateText();
    }

    return () => {
      loopingRef.current = false;
      if (typeSpeedDelayRef.current) {
        typeSpeedDelayRef.current.cancel();
      }
    };
  }, [props.text, animateText]); // Re-run when text changes

  return (
    <div className={'dialog-box-wrap'}>
      <div style={{ ...props.textStyle }} className="dialog-box-text">
        <p>{text}</p>
        <div
          className={`dialog-box-pointer ${blink && 'animate-pointer'} ${hideCursor ? 'hide-pointer' : ''}`}
        ></div>
      </div>
    </div>
  );
};

DialogBox.propTypes = propTypeValidation;

export default DialogBox;
